.messageMain {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.img {
  padding: 15px 0px 0px 0px;
  height: 50px;
  align-items: flex-end;
  border-radius: 50%;
}

.messageColumn {
  margin-left: 1rem;
  position: relative;
  background-color: #add9a4;
  border-radius: 10px 10px 10px 0;
  color: white;
}
.messageColumn:before {
  content: '';
  position: absolute;
  bottom: 0;
  left: -40px;
  height: 20px;
  width: 40px;
  border-bottom-right-radius: 50%;
  box-shadow: 20px 0 0 0 #add9a4;
}

.message {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.nameUser {
  padding-top: 0.2rem;
  padding-right: 0.2rem;
  padding-left: 0.2rem;
}
.messageUser {
  padding: 5px 10px 5px 10px;
  font-size: 15px;
}
.timeUser {
  font-size: 10px;
  text-align: end;
  padding-bottom: 0.2rem;
  padding-right: 0.4rem;
}
